// AI Symptom Checker action
export async function checkSymptoms(
  prevState: any,
  formData: FormData
) {
  const symptoms = formData.get("symptoms") as string;
  if (!symptoms || symptoms.trim().length < 10) {
    return { error: "Please describe your symptoms in at least 10 characters." };
  }

  try {
    const input: AISymptomCheckerInput = { symptoms };
    const result = await aiSymptomChecker(input);
    return { data: result };
  } catch (error) {
    console.error(error);
    return { error: "An error occurred while analyzing your symptoms. Please try again." };
  }
}

// Explain Cure action
export async function explainCureAction(
  prevState: any,
  formData: FormData
) {
  const symptoms = formData.get("symptoms") as string;
  const cure = formData.get("cure") as string;

  if (!symptoms || !cure) {
    return { error: "Missing information to explain the cure." };
  }
  
  try {
    const input: ExplainCureInput = { symptoms, cure };
    const result = await explainCure(input);
    return { data: result };
  } catch (error) {
    console.error(error);
    return { error: "An error occurred while generating the explanation. Please try again." };
  }
}
